
.board canvas[data-layer-id=canvas-cover] {
  z-index: 1 !important;
  pointer-events: none;
  background: rgba(255, 255, 255, 0);
}
.actions {
  width: 100%;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
}
.actions .tools {
    box-sizing: border-box;
    padding: 0 20px;
    border-right: 1px solid #ddd;
}
.actions .tools ul {
      display: -ms-flexbox;
      display: flex;
      margin: 0;
      padding: 0;
}
.actions .tools ul li {
        list-style-type: none;
        padding: 15px;
        height: 54px;
        text-align: center;
        box-sizing: border-box;
        cursor: pointer;
}
.actions .tools ul li:hover {
          background-color: #eee;
}
.actions .tools ul li.selected {
          background-color: #eee;
}
.actions .tools ul li i {
          font-size: 16px;
}
.actions .tools ul li i.disabled {
            color: #ccc;
}
.actions .tools.props ul li {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-align: center;
          align-items: center;
}
.actions .tools.props ul li:hover {
        background-color: #fff;
}
.actions .tools.props ul li.selected {
        background-color: #fff;
}
.actions .tools.props ul li .el-input--suffix .el-input__inner {
        padding-right: 0 !important;
}
.actions .tools.props ul li .el-input {
        width: 63px;
}
.actions .tools.props ul li .el-input .el-input__inner {
          border: 0;
}
.canvas-container.eraser canvas {
  cursor: none;
}
.canvas-container canvas {
  cursor: crosshair;
  background-color: #fff;
}
